          top: 0;
          left: 50%;
          transform: translate(-50%, -100%);
          transition: 0.3s ease-in-out;
        }

        .project-description {
          font-size: 15px;
          width: 100%;
          text-align: center;
          position: absolute;
          font-family: "BarlowCondensed-Light", sans-serif;
          color: $primary-dark-color-palette;
          top: 100%;
          left: 50%;
          transform: translate(-50%, 100%);
          transition: 0.3s ease-in-out;
        }

        &.active {

          .project-image {
            transform: none;
          }

          &:hover {
            .project-name {
              top: 35%;
              transform: translate(-50%, -50%);
            }
            .project-description {
              top: 65%;
              transform: translate(-50%, -50%);
            }

            .project-image {
              transform: scale(1.2);

              &:after {
                opacity: 1;
              }
            }
          }
        }

      }

    }

    .contact-text {
      position: absolute;
      left: 75px;
      top: calc(50% - 50px);
      width: 30%;
      transform: translateY(-50%);
      font-size: 17px;
      line-height: 1.2;
    }

    .contact-form {
      position: absolute;
      display: flex;
      width: 50%;
      top: calc(50% - 25px);
      left: 70%;
      transform: translate(-50%, -50%);
      justify-content: space-between;
      flex-wrap: wrap;

      input, textarea, button {
        background-color: #121e26;
        color: white;
        font-size: 18px;
        resize: none;
        font-style: normal;
        font-family: "BarlowCondensed-Light", sans-serif;
        padding: 10px;
        width: 100%;
        height: 100%;

        &:focus  {
          outline: 0;

          +.input-border {
            width: 100%;
          }
        }
      }

      .input-border {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 0%;
        height: 3px;
        background-color: $secondary-color-palette;
        transition: 0.2s ease-in-out;
      }

      .name-input, .email-input {
        width: 49%;
        height: 40px;
        position: relative;
      }

      .message-input {
        width: 100%;
        position: relative;
        height: 150px;
        margin: 2% 0;
      }

      .submit-input {
        cursor: pointer;
        height: 40px;
        width: 35%;
        position: relative;
        transform: scale(1);
        transition: 0.1s ease-in-out;

        span:nth-child(1) {
          top: 0;
          left: 0;
          width: 100%;
          height: 3px;
          transform-origin: right;
          transition-delay: 0s;
        }
        span:nth-child(2) {
          top: 0;
          right: 0;
          width: 3px;
          height: 100%;
          transform-origin: bottom;
          transition-delay: 0.1s;
        }
        span:nth-child(3) {
          bottom: 0;
          right: 0;
          width: 100%;
          height: 3px;
          transform-origin: left;
          transition-delay: 0.2s;
        }
        span:nth-child(4) {
          bottom: 0;
          left: 0;
          width: 3px;
          height: 100%;
          transform-origin: top;
          transition-delay: 0.3s;
        }

        &:hover {
          .submit-border {
            transform: scale(1);
          }

          span:nth-child(1) {
            transform-origin: left;
          }
          span:nth-child(2) {
            transform-origin: top;
          }
          span:nth-child(3) {
            transform-origin: right;
          }
          span:nth-child(4) {
            transform-origin: bottom;
          }
        }

        .submit-border {
          transform: scale(0);
          background-color: $secondary-color-palette;
          position: absolute;
          transition: transform 0.1s linear;
        }

        &:active {
          transform: scale(0.95);
          background-color: $secondary-color-palette;
          color: $primary-dark-color-palette;
          font-weight: bold;
        }

      }

    }

    .skill-box {
      background-color: $primary-middle-light-color-palette;
      position: absolute;
      transform: translate(-50%, -50%);
      text-align: center;
      transition: 0.3s ease-in-out;
      border: 2px solid $primary-middle-light-color-palette;
      height: 75px;

      &:hover {
        border: 2px solid $secondary-color-palette;
      }


      .skill-name {
        position: absolute;
        font-family: "Josefin Sans", sans-serif;
        top: 0;
        left: 0;
        transform: translate(0%, -90%);
        font-size: 15px;
        color: $secondary-color-palette;
      }

      .skill-description {
        font-size: 12px;
        position: relative;
        padding: 5px;
      }

      .skill-list {
        padding: 10px;
        font-size: 30px;
        font-family: "BarlowCondensed-Medium", sans-serif;
        display: flex;
        justify-content: space-evenly;
        width: 100%;
        height: 75px;

        .item {
          position: relative;

          .item-name {
            font-size: 11px;
            position: absolute;
            left: 50%;
            bottom: 0;
            transform: translateX(-50%);
          }
        }

      }


      &.backend-skills {
        top: calc(30% - 25px);
        left: 50%;
        width: 80%;
      }

      &.frontend-skills {
        top: calc(55% - 50px);
        left: 50%;
        width: 80%;
      }

      &.miscellaneous-skills {
        top: calc(80% - 75px);
        left: 50%;
        width: 80%;
      }

    }

  }

  .mention {
    position: absolute;
    bottom: 5px;
    left: 50%;
    transform: translateX(-50%);
    font-size: 10px;
    color: #b3b3b3;
  }
}