@import "_variables";

@font-face {
  font-family: 'BarlowCondensed-Medium';
  src: local("BarlowCondensed"), url("../assets/fonts/BarlowCondensed-Medium.ttf") format("truetype");
}

@font-face {
  font-family: 'BarlowCondensed-Light';
  src: local("BarlowCondensed"), url("../assets/fonts/BarlowCondensed-Light.ttf") format("truetype");
}

@font-face {
  font-family: "Josefin Sans";
  src: local("Josefin Sans"), url("../assets/fonts/JosefinSans-Medium.ttf") format("truetype");
}

* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}

*:not(input, textarea) {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

body {
  background: $primary-dark-color-palette;
  color: $light-color-palette;
  width: 100%;
  height: 100vh;
  font-family: "BarlowCondensed-Light", sans-serif;

  &.autoscroll {
    overflow: hidden;
  }
}

strong {
  font-family: "BarlowCondensed-Medium", sans-serif;
  color: $secondary-color-palette;
  font-weight: initial;
}