@import "../../sassStyles/variables";


@media screen and (min-width: 768px) {
  #home-page {
    .title {
      position: absolute;
      top: 50%;
      transform: translate(0, -50%);
      left: 7vw;
      transition: 0.5s ease-in-out;
      opacity: 1;

      .title1 {
        font-family: "BarlowCondensed-Medium", sans-serif;
        font-size: 3vw;
        line-height: 2;
      }

      .title2 {
        font-family: "Josefin Sans", sans-serif;
        color: $secondary-color-palette;
        font-size: 5vw;
        font-weight: bold;
        line-height: 1;

        .firstName {
          display: block;
        }

        .firstLetter {
          text-shadow: 1px 2px 0 $primary-light-color-palette;
        }
      }

      .title3 {
        font-family: "BarlowCondensed-Medium", sans-serif;
        font-size: 2.5vw;
        line-height: 2;
      }

      &.hidden {
        transform: translate(calc(-100% - 30px), -50%);
        opacity: 0;
      }

    }

    .logo {
      cursor: default;
      position: absolute;
      top: 50%;
      left: 65%;
      transform: translate(-50%, -50%) scale(1);
      transition: 0.7s ease-out;
      font-family: "Josefin Sans", sans-serif;
      font-size: 30vw;
      color: $secondary-color-palette;
      text-shadow: 2px 5px 0 $primary-light-color-palette;
      animation: float 4s infinite ease-in-out 0.7s;

      &.hidden {
        transform: translate(-50%, -50%) scale(0) rotate(45deg);
        animation: none;
      }
    }

  }
}

@media screen and (max-width: 767px) {
  #home-page {
    .title {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      transition: 0.5s ease-in-out;
      opacity: 1;

      .title1 {
        font-family: "BarlowCondensed-Medium", sans-serif;
        font-size: 8vw;
        line-height: 2;
      }

      .title2 {
        font-family: "Josefin Sans", sans-serif;
        color: $secondary-color-palette;
        font-size: 9vw;
        font-weight: bold;
        line-height: 1;

        .firstLetter {
          text-shadow: 1px 1px 0 $primary-light-color-palette;
        }
      }

      .title3 {
        font-family: "BarlowCondensed-Medium", sans-serif;
        font-size: 6vw;
        line-height: 2;
      }

      &.hidden {
        transform: translate(calc(-100% - 30px), -50%);
        opacity: 0;
      }

    }

    .logo {
      display: none;
    }

  }
}

@keyframes float {
  0% {
    transform: translate(-50% , -50%);
  }
  50% {
    transform: translate(calc(-50% + 10px), calc(-50% + 20px)) scale(1.1) rotate(5deg);
  }
  100% {
    transform: translate(-50% , -50%);
  }
}