@import "../../sassStyles/_variables";


@media screen and (min-width: 768px) {
  #navBar {
    position: fixed;
    width: 6vw;
    height: 100%;
    text-align: center;
    z-index: 100;
    transition: 0.5s ease-in-out;
    a {
      cursor: default;
      text-decoration: none;
      color: $light-color-palette;
      font-size: 3vw;

      &.enable {
        cursor: pointer;

        svg:hover {
          transform: rotate(10deg);
        }

      }

      &.active {
        color: $secondary-color-palette;
      }

      svg {
        margin: 1vw 0;
        transition: 0.2s;
      }
    }

    nav {
      position: absolute;
      top: 50%;
      left: 100%;
      transform: translate(-100%, -50%);
    }

  }
}

@media screen and (max-width: 767px) and (orientation: portrait) {
